<template>
  <transition name="slide">
    <div class="wrapper">
      <div class="header">
        <i class="back" @click="back"></i>
        <span class="text">基本信息</span>
      </div>
      <div class="content-wrapper">
        <scroll class="content">
          <div>
            <div class="slider-wrapper">
              <slider>
                <div v-for="item in imgList">
                  <img :src="item" />
                </div>
              </slider>
              <div class="store">
                <div class="img-wrapper">
                  <img :src="merchantInfo.logoUrl" alt="">
                </div>
                <div class="text">
                  {{merchantInfo.name}}
                </div>
              </div>
            </div>
            <ul class="list">
              <li class="item">
                <div class="left alliance">
                  <div class="icon-wrapper">
                    <i></i>
                  </div>
                  <span>是否联盟</span>
                </div>
                <div class="right">{{chainName}}</div>
              </li>
              <li class="item" v-if="false">
                <div class="left id">
                  <div class="icon-wrapper">
                    <i></i>
                  </div>
                  <span>总店ID</span>
                </div>
                <div class="right">{{merchantInfo.merchantId}}</div>
              </li>
            </ul>
            <ul class="list">
              <li class="item">
                <div class="left total">
                  <div class="icon-wrapper">
                    <i></i>
                  </div>
                  <span>应还账单总额</span>
                </div>
                <div class="right">{{merchantInfo.billMoney|formatMoney}}</div>
              </li>
              <li class="item">
                <div class="left remain">
                  <div class="icon-wrapper">
                    <i></i>
                  </div>
                  <span>剩余补贴额度</span>
                </div>
                <div class="right">{{merchantInfo.subsidyMoney|formatMoney}}</div>
              </li>
              <li class="item">
                <div class="left period">
                  <div class="icon-wrapper">
                    <i></i>
                  </div>
                  <span>订单结算周期</span>
                </div>
                <div class="right">{{merchantInfo.checkCycleDay}}</div>
              </li>
            </ul>
            <ul class="list">
              <li class="item">
                <div class="left person">
                  <div class="icon-wrapper">
                    <i></i>
                  </div>
                  <span>联系人</span>
                </div>
                <div class="right">{{merchantInfo.contacts}}</div>
              </li>
              <li class="item">
                <div class="left mobile">
                  <div class="icon-wrapper">
                    <i></i>
                  </div>
                  <span>联系电话</span>
                </div>
                <div class="right">{{merchantInfo.contactsNumber}}</div>
              </li>
              <li class="item">
                <div class="left location start">
                  <div class="icon-wrapper">
                    <i></i>
                  </div>
                  <span>联系地址</span>
                </div>
                <div class="right">{{merchantInfo.fullAddress}}</div>
              </li>
              <li class="item" v-if="false">
                <div class="lati left">
                  <div class="icon-wrapper">
                    <i></i>
                  </div>
                  <span>经度</span>
                </div>
                <div class="right">东经E114°10′3.28″</div>
              </li>
              <li class="item" v-if="false">
                <div class="lati left">
                  <div class="icon-wrapper">
                    <i></i>
                  </div>
                  <span>纬度</span>
                </div>
                <div class="right">北纬N22°33′45.51″</div>
              </li>
            </ul>
            <ul class="list" v-if="false">
              <li class="item">
                <div class="left bank start">
                  <div class="icon-wrapper">
                    <i></i>
                  </div>
                  <span>银行信息</span>
                </div>
                <div class="right">
                  <label>华夏银行</label>
                  <span>6228480402564890018</span>
                </div>
              </li>
            </ul>
            <div class="article">
              <h3>简介</h3>
              <div v-html="merchantInfo.description">
              </div>
            </div>
          </div>
        </scroll>
      </div>
    </div>
  </transition>
</template>

<script type="text/ecmascript-6">
  import Scroll from 'base/scroll/scroll'
  import Slider from 'base/slider/slider'
  import {getCurrentMerchant} from 'api/mine'
  import {ERR_CODE,ORIGIN} from 'api/config'

  export default {
    data () {
      return {
        imgList: [],
        merchantInfo: {}
      }
    },
    created() {
      this._getImgList()
      this._getCurrentMerchant()
    },
    computed: {
      chainName(){
        return this.merchantInfo.chainName || '否'
      }
    },
    methods: {
      back() {
        this.$router.back()
      },
      _getCurrentMerchant(){
        getCurrentMerchant().then((res) => {
          if(res.errCode === ERR_CODE){
            this.merchantInfo = res.data
          }
        })
      },
      _getImgList() {
        for(let i = 0; i < 1; i++){
          this.imgList.push('https://y.gtimg.cn/music/photo_new/T003R720x288M000000yTU5u2XJFWh.jpg?max_age=2592000')
        }
      }
    },
    components: {
      Scroll,
      Slider
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"

  .slide-enter,.slide-leave-to
    transform translate3d(100%,0,0)

  .slide-enter-active,.slide-leave-active
    transition all 0.3s

  .wrapper
    position absolute
    left 0
    right 0
    top 0
    bottom 0
    z-index 1
    background $color-background
    .header
      position fixed
      height 44px
      top 0
      left 0
      right 0
      background $color-header-background
      display flex
      justify-content center
      align-items center
      .back
        position absolute
        background url("~common/image/back.png") no-repeat
        background-size contain
        width 8px
        height 15px
        left 15px
        top 15px
        &:before
          position absolute
          left -10px
          right -10px
          top -10px
          bottom -10px
          content ''
      .text
        color $color-header-text
        font-size $font-size-large
        font-weight bold
    .content-wrapper
      position absolute
      left 0
      right 0
      top 44px
      bottom 10px
      display flex
      .content
        width 100%
        height 100%
        overflow hidden
        .slider-wrapper
          width 100%
          position relative
          overflow hidden
          .store
            left 15px
            bottom 15px
            position absolute
            display flex
            .img-wrapper
              width 70px
              height 70px
              img
                display block
                width 100%
                height 100%
            .text
              color #fff
              font-size $font-size-large
              margin-left 16px
              display flex
              align-items flex-end
        .list
          margin-top 10px
          background #fff
          .item
            border-bottom 1px solid #e6e6e6
            padding-right 15px
            margin-left 15px
            display flex
            justify-content space-between
            line-height 42px
            .left,.right
              font-size $font-size-medium
              color #3d3d3d
            .left
              text-align left
              display flex
              align-items center
              .icon-wrapper
                width 30px
                display flex
              &.alliance i
                width 16px
                height 16px
                background url("~common/image/mine/alliance.png") no-repeat
                background-size contain
              &.id i
                width 18px
                height 18px
                background url("~common/image/mine/id.png") no-repeat
                background-size contain
              &.total i
                width 17px
                height 14px
                background url("~common/image/mine/total.png") no-repeat
                background-size contain
              &.remain i
                width 18px
                height 14px
                background url("~common/image/mine/alliance.png") no-repeat
                background-size contain
              &.period i
                width 16px
                height 16px
                background url("~common/image/mine/period.png") no-repeat
                background-size contain
              &.person i
                width 18px
                height 14px
                background url("~common/image/mine/person.png") no-repeat
                background-size contain
              &.mobile i
                width 15px
                height 15px
                background url("~common/image/mine/mobile.png") no-repeat
                background-size contain
              &.location i
                width 18px
                height 18px
                background url("~common/image/mine/location.png") no-repeat
                background-size contain
                margin-top 12px
              &.lati i
                width 15px
                height 15px
                background url("~common/image/mine/lati.png") no-repeat
                background-size contain
              &.bank i
                width 20px
                height 20px
                background url("~common/image/mine/bank.png") no-repeat
                background-size contain
                margin-top 14px
              &.start
                align-items flex-start
            .right
              text-align right
              font-weight bold
              max-width 160px
              display flex
              flex-direction column
              justify-content center
              label,span
                font-weight bold
                max-width 200px
            &:last-child
              border-bottom none
        .article
          margin-top 10px
          margin-bottom 56px
          background #fff
          box-sizing border-box
          padding-left 15px
          padding-right 15px
          padding-bottom 15px
          line-height 25px
          color #333
          font-size $font-size-medium-m
          h3
            height 48px
            display flex
            align-items center
            font-size $font-size-large-m
            font-weight bold
          p
            text-indent 2em
</style>


